import React from "react";
import { Group, Div, Button, Panel, PanelHeader } from "@vkontakte/vkui";
import connect from "@vkontakte/vk-bridge";
import "./ad.css";

function balancLvL() {
  connect.sendPromise("VKWebAppShowNativeAds", { ad_format: "preloader" });
}
function balancLvL2() {
  connect.sendPromise("VKWebAppShowNativeAds", { ad_format: "reward" });
}
function balancLvL3() {
  connect.sendPromise("VKWebAppShowNativeAds", { ad_format: "interstitial" });
}
const Ad = (props) => (
  <Panel id={props.id}>
    <PanelHeader>Реклама</PanelHeader>
    <Group style={{ height: "500px" }}>
      <Div align="center"></Div>
      <br></br>
      <br></br>
      <center>
        <Button size="xl" level="2" onClick={() => balancLvL()}>
          preloader
        </Button>
      </center>
      <br></br>
      <br></br>
      <center>
        <Button size="xl" level="2" onClick={() => balancLvL2()}>
          reward
        </Button>
      </center>
      <br></br>
      <br></br>
      <center>
        <Button size="xl" level="2" onClick={() => balancLvL3()}>
          interstitial
        </Button>
      </center>
    </Group>
  </Panel>
);

export default Ad;
